import {
  Button,
  defaultTheme,
  Heading,
  Pane,
  ThemeProvider,
} from "evergreen-ui";
import { Outlet } from "react-router-dom";
import { Data } from "./Projects/Data";

function AccountButton(): JSX.Element {
  return <Button appearance="primary">Primary Button</Button>;
}

function ConnectButton(): JSX.Element {
  return <Button appearance="primary">Primary Button</Button>;
}

function Navigation(): JSX.Element {
  return (
    <Pane display="flex" padding={16} background="tint2" position="sticky" top={0} left={0}>
      <Pane flex={1} alignItems="center" display="flex">
        <Heading size={600}>Left Aligned</Heading>
      </Pane>
      <Pane>
        {/* Below you can see the marginRight property on a Button. */}
        <ConnectButton />
        <AccountButton />
      </Pane>
    </Pane>
  );
}

export const Layout = () => (
  <ThemeProvider value={defaultTheme}>
    <Navigation />
    <Outlet />
  </ThemeProvider>
);

export const Projects = () => <Data />;
